h2 {
    font-size: 22px;
}
.Wrapper{
    display: flex;
    flex-direction: column;
    /*border: 1px solid #ddd;*/
    flex:1;
    margin-right: 30px;
    margin-left: -50px;
    /*align-items: center;*/
}
.cv {
    text-decoration: none;
    font-size: 25px;
    color: rgba(15, 15, 15, 0.97);
}
.second_row{
    display: flex;
    align-items: flex-start;
    width: 100%;
    margin: 10px auto;
    padding: 10px 0;
    justify-content: space-between;
}
.News{
    width: 120%;
    text-align: left;
    margin-top: 50px;
}
.icon-links{
    display: flex;
    justify-content: center;
    gap: 20px; /* 调整元素之间的间隔 */
    color: black;
}
.photo {
    width: 70%;
    padding: 60px;
    text-align: center;
}
.photo img {
    max-width: 220px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.intro {
    width: 64%;
    padding: 10px;
    text-align: left;
    /*border: 1px solid #ddd;*/
    flex:2;
}
.intro h1 {
    margin-top: 0;
}
.projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
}
.projects-container img,video {
    width:380px;
    height:212px;
    transform: scale(1, 1.13);
    margin-bottom: -2px;
}

.projects-container a:hover {
    font-weight: bold;
}

@media screen and (min-width: 700px) {
    .whole{
        font-family:  "Gill Sans MT", sans-serif;
        font-size: 16px;
        width: 100%; /* 设置容器宽度为100% */
        /*display: flex; !* 使用flex布局 *!*/
        justify-content: center; /* 在容器内水平居中 */
    }
    .container {
        display: flex;
        max-width: 1200px; /* 设置内容的最大宽度 */
        width: 100%; /* 设置内容宽度为100% */
        margin: 0 auto;
    }
}

@media screen and (max-width: 700px) {
    .whole {
        /* 在显示器宽度小于600像素时，修改样式 */
        max-width: 90%; /* 最大宽度改为100% */
        /*padding: 0 20px; !* 添加一些内边距 *!*/
        font-family:  "Gill Sans MT", sans-serif;
        font-size: 16px;
        justify-content: center; /* 在容器内水平居中 */
    }
    .container {
        flex-direction: column;
    }
    .photo{
        flex: none;
        width: 80%;
    }
    .intro{
        width: 105%;
    }
}
.intro a {
    text-decoration: underline;
    text-decoration-color: #48D1CC;
}
.intro a:hover {
    background-color: #48D1CC;
}
a {
    color: black;
}
.fa-twitter:hover {
    color: #48D1CC;
}
.fa-github:hover {
    color: #48D1CC;
}
.fa-envelope:hover {
    color: #48D1CC;
}
.cv:hover {
    color: #48D1CC;
}
footer {
    text-align: center;
}
